<template>
  <div class="page page-user-nickname">
    <van-field v-model="nickname" autocomplete="new-password" placeholder="请输入用户昵称"></van-field>
    <div class="tip">用户昵称为4-20位字符</div>
  </div>
</template>

<script>
import {editNickName} from "@/apis/user";

export default {
  data() {
    return {
      nickname: "",
    };
  },
  created() {
    this.nickname = this.$root.userInfo.nickname;
    this.$root.$emit("root:set-topbar", ["left-back", "custom-btn"], {
      label: "保存",
      onClick: this.save,
    });
  },
  methods: {
    save() {
      if (this.nickname.length < 4 || this.nickname.length > 20)
        return this.$toast.fail("用户昵称为4-20位字符");
      editNickName({
        nickname: this.nickname,
      }).then(({ok, msg}) => {
        if (ok) {
          this.$root.userInfo.nickname = this.nickname;
          this.$toast.success(msg);
          this.$router.back();
        } else {
          this.$toast.fail(msg);
        }
      });
    },
  },
};
</script>

<style lang="scss">
.page-user-nickname {
  .van-field {
    background-color: #f0f0f0ff;
    margin: 15px 0 10px;
  }

  .tip {
    font-size: 12px;
    color: #999999ff;
    padding-left: 12px;
  }
}
</style>
